import React from 'react';
import Slider from 'react-slick';
import styled from 'styled-components';
import '../css/slick.css';
import '../css/slick-theme.css';

const Image = styled.img`
  margin: auto;
  width: 1103px;
  height: 612px;
`;

const MainSlideImage = () => {
  const settings = {
    slide: 'div', //    ex) div, li
    dots: true, //   ( )
    infinite: true, //  
    speed: 500, //      (ms)
    slidesToShow: 1, //    
    slidesToScroll: 1, //    
    autoplay: true, //   
    autoplaySpeed: 10000, //      (ms)
    vertical: false, //   true:, false:
    arrows: true, //    
    prevArrow: (
      <img src={`${process.env.PUBLIC_URL}/images/left-arrow.png`} />
    ),
    nextArrow: (
      <img src={`${process.env.PUBLIC_URL}/images/right-arrow.png`} />
    ),
    dotsClass: 'slick-dots', //  () css class 
    dragable: 'true', //  
  };

  return (
    <div>
      <Slider {...settings}>
        <div>
          <Image
            src={process.env.PUBLIC_URL + '/images/image01.png'}
          />
        </div>
        <div>
          <Image
            src={process.env.PUBLIC_URL + '/images/image02.jpg'}
          />
        </div>
        <div>
          <Image
            src={process.env.PUBLIC_URL + '/images/image03.jpg'}
          />
        </div>
      </Slider>
    </div>
  );
};

export default MainSlideImage;
