import React, { useEffect, useState } from 'react';
import Slider from 'react-slick';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import MainSlideImage from './MainSlideImage';
import { fireStore } from '../../src/firebase'; //firebase.js  fireStore
import { collection, getDocs, query, where } from 'firebase/firestore';

const settings = {
  slide: 'div', //    ex) div, li
  dots: true, //   ( )
  rows: 2, // ?
  slidePerRow: 4, //slidesToShow, Scroll    .
  infinite: true, //  
  speed: 1000, //      (ms)
  slidesToShow: 4, //    
  slidesToScroll: 4, //    
  autoplay: true, //   
  autoplaySpeed: 7500, //      (ms)
  vertical: false, //   true:, false:
  arrows: true, //    
  dotsClass: 'slick-dots', //  () css class 
  dragable: 'true', //  
};

//  Box Contatiner Div (styledComponent)
const ContainerIntroDiv = styled.div`
  max-width: 1100px;
  min-width: 900px;
  width: 1100px;
  height: 180px;
  margin: 40px auto;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
`;

//  Box Items Div (styledComponent)
const ItemIntroDiv = styled.div`
  width: 230px;
  height: 150px;
  border: 1px solid lightgray;
  padding-left: 25px;

  & > .colorBox {
    margin-top: 25px;
    width: 20px;
    height: 2.5px;
    background-color: ${(props) => props.$boxColor};
  }

  & > .title {
    font-size: 11px;
    color: gray;
  }

  & > .subtitle {
    font-weight: 600;
  }

  & > .contents {
    font-size: 11px;g
    color: gray;
  }
`;

// Slider     
// >  &    ( & img)
const StyledSlider = styled(Slider)`
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  max-width: 1100px;
  min-width: 900px;
  width: 1100px;
  height: 660px;
  border: 1px solid black;
  margin: auto;

  & img {
    margin-bottom: 10px;
    width: 230px;
    height: 315px;
  }

  .slick-prev {
    display: none !important;
  }

  .slick-next {
    display: none !important;
  }
`;

const SiteIntroductionBox = () => {
  return (
    <div>
      <ContainerIntroDiv>
        <ItemIntroDiv $boxColor="black">
          <div className={'colorBox'} />
          <p className={'title'}>POOMZIL BAD</p>
          <p className={'subtitle'}>WORST POOMZIL</p>
          <p className={'contents'}>
               
            <br />
              
          </p>
        </ItemIntroDiv>
        <ItemIntroDiv $boxColor={'grey'}>
          <div className={'colorBox'} />
          <p className={'title'}>SPEED OF DELIVERY</p>
          <p className={'subtitle'}>TAKES A LONG TIME</p>
          <p className={'contents'}>
               ,
            <br />
               .
          </p>
        </ItemIntroDiv>
        <ItemIntroDiv $boxColor="blue">
          <div className={'colorBox'} />
          <p className={'title'}>NO REFUNDS</p>
          <p className={'subtitle'}>NEVER. NEVER.</p>
          <p className={'contents'}>
            '/  .'
            <br />
            '/  .'
          </p>
        </ItemIntroDiv>
        <ItemIntroDiv $boxColor="green">
          <div className={'colorBox'} />
          <p className={'title'}>COST REDUCTION</p>
          <p className={'subtitle'}>BUT EXPENSIVE</p>
          <p className={'contents'}>
            ' .'
            <br />
            '   .'
          </p>
        </ItemIntroDiv>
      </ContainerIntroDiv>
      {/*  Box..End*/}
      {/* 1. WEEKLY BEST ITEMS  16*/}
      {/* 1-1.   WEEKLY BEST ITEMS */}
      <h2 style={{ textAlign: 'center' }}>WEEKLY BEST ITEMS</h2>
      <p
        style={{
          textAlign: 'center',
          color: '#8A8989',
          marginTop: '-5px',
        }}
      >
          
      </p>
    </div>
  );
};

const Main = () => {
  /** fireStore collection "shopping_itmes"  document    */
  const itemsRef = collection(fireStore, 'shopping_items');
  /** query  ,  1 document, 2 where
   *       */
  const resultQuery = query(itemsRef, where('ITEMS_SHOWMAIN', '==', 'O'));
  const [ItemsList, setItemsList] = useState([]);

  //  Loading 
  useEffect(() => {
    const TmpItemList = [];
    const getItems = async () => {
      const data = await getDocs(resultQuery);
      data.forEach((doc) => {
        TmpItemList.push(doc.data());
        setItemsList(TmpItemList);
      });
    };
    getItems();
  }, []);

  return (
    <div>
      <MainSlideImage /> {/* 7 Slide */}
      <SiteIntroductionBox /> {/* .Box 4 flex */}
      <StyledSlider {...settings}>
        {ItemsList.map((ele, idx) => (
          <div key={idx}>
            <Link to="/product">
              <img src={ele['ITEMS_IMGURL']} alt="" />
            </Link>
          </div>
        ))}
      </StyledSlider>
    </div>
  );
};

export default Main;
